import ticketOrder from '../../components/ticket-order'
import goodsOrder from '../../components/goods-order'
export default {
    data() {
        return {
            orderArr: [],
            pageNum:1,
            pageSize:100
        }
    },
    components: {
        ticketOrder,
        goodsOrder
    },
    created() {
        this.getOrderList()
    },
    onPullDownRefresh() {        
        this.getOrderList()
        setTimeout(function () {
            uni.stopPullDownRefresh();
        }, 1000);
    },
    methods: {
        async getOrderList () {
            let params = {
                pageNum: this.pageNum,
                pageSize: this.pageSize
            }
            let res = await this.$api.getOrderList(params)
            if (res.code === '0') {
                this.orderArr = res.data.list
            }
        },
        reflash () {
            setTimeout(() => {          
                this.getOrderList()
            }, 2000);
        }
    },
}
